.recent-news{
	background-color: var(--color-light);
	padding-bottom: 117px;

	&__title{
		font-weight: 700;
		font-size: 46px;
		line-height: 130%;

		color: var(--color-ebony);
		margin-bottom: 62px;
	}

	&__list{
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		grid-template-rows: repeat(2, 255px);
		gap: var(--grid-gap);

		margin-bottom: 79px;
	}

	&__item{
		grid-column: 5 span;
		&:first-child{
			grid-column: 7 span;
			grid-row: 2 span;	
		}
		
	}
}

.recent-article{
	overflow: hidden;
	border-radius: 4px;
	height: 100%;

	box-shadow: 
	0px 80px 80px -20px rgba(154, 156, 165, 0.08),
	0px 30px 24px -10px rgba(154, 156, 165, 0.05),
	0px 12px 10px -6px rgba(154, 156, 165, 0.04),
	0px 4px 4px -4px rgba(30, 33, 44, 0.03);

	background-color: var(--color-light);

	&--small{
		.recent-article__descr{
			display: none;
		}
	}

	&__image{
		border-radius: 4px 4px 0px 0px;

		img{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text{
		padding: 25px 23px;

		&-small{
			padding: 18px 24px;
		}
	}

	&__title{
		margin-bottom: 12px;
	}

	&__link{
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;

		color: var(--color-ebony);
	}

	&__descr{
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;

		color: var(--color-gray);	
	}

}

.article-meta{
	font-weight: 400;
	font-size: 14px;
	line-height: 150%;
	
	color: var(--color-storm);

	margin-bottom: 16px;	

	a{
		color: inherit;
	}

	&__time{
		position: relative;
		margin-left: 21px;
		margin-right: 9px;

		&::after{
			content: "";
			position: absolute;
			width: 1px;
			height: 12px;
			bottom: 1px;
			right: -12px;
			background-color: var(--color-storm);
		}

		&::before{
			content: "";
			position: absolute;
			width: 1px;
			height: 12px;
			bottom: 1px;
			left: -12px;
			background-color: var(--color-storm);
		}
	}

	&__comments{
		padding-left: 37px;
		background-repeat: no-repeat;
		background-size: 16px 16px;
		background-position: 13px 0;
		background-image: url("../img/comments-icon.svg");
	}

}